import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Checkbox, Table } from 'antd';
import { CartHeadAction } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudentField,
  getStudentWithFields,
  exportStudentWithFields,
} from 'services/exportStudentsByField';
import BtnExport from 'components/common/button/BtnExport';
import SearchBar from './SearchBar';
import './style.scss';

const BrowserText = (function () {
  const canvas = document.createElement('canvas');
  const context = canvas.getContext('2d');

  function getWidth(text, fontSize, fontFace) {
      context.font = `${fontSize}px ${fontFace}`;
      return context.measureText(text).width;
  }

  return {
      getWidth,
  };
})();

@Form.create({ name: 'formListSubsidyObject' })
class ListSubsidyObject extends Component {
  state = {
    loadingFields: false,
    loadingStudents: false,
    paramSearch: {},
    rawDataFields: [],
    dataFields: [],
    dataStudent: [],
    columnsField: [],
    columnsFilter: [],
    totalXOfFields: 0,
    totalXOfFilter: 0,
  };
  
  componentDidMount = async () => {
    this.handleChangeQueryParams();
    this.setState({
      loadingFields: true,
    });
    const res = await getStudentField();
    let totalXOfFields = 0;
    const columnsField = res.data.map( item => {
      const columnObj = {
        title: item.label,
        dataIndex: item.field,
        key: item.field,
      };
      const tempWidth = BrowserText.getWidth(item.label, 14, 'sans-serif');
      columnObj.width = tempWidth < 100 ? 100 : tempWidth + 20;
      totalXOfFields += columnObj.width;
      columnObj.render = (text, record) => <Checkbox checked={record[item.field]} onChange={() => this.handleFieldsChange(item.field)} />;
      return columnObj;
    });
    const dataFields = [{
      customIdForThis: 999999,
    }];
    res.data.forEach(item => {
      dataFields[0][item.field] = item.fieldChecked;
    });
    this.setState({
      columnsField,
      dataFields,
      totalXOfFields,
      rawDataFields: res.data,
      loadingFields: false,
    });
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    const { columnsFilter } = this.state;
    this.setState({
      loadingStudents: true,
    });
    const paramSearch = getQueryParams();
    paramSearch.fieldList = columnsFilter.map(item => ({
      field: item.key, 
      label: item.title, 
      fieldChecked: true
    }));
    const res = await getStudentWithFields(paramSearch);
    this.setState({
      paramSearch,
      dataStudent: res.data.studentList,
      loadingStudents: false,
    });
  };

  handleFieldsChange = (field) => {
    const { dataFields, columnsField, columnsFilter } = this.state;
    dataFields[0][field] = !dataFields[0][field];
    const isExist = columnsFilter.some(item => item.key === field);
    let tempColumnsFilter = [...columnsFilter];
    if(isExist) {
      tempColumnsFilter = tempColumnsFilter.filter(item => item.key !== field);  
    } else {
      const oneColumn = columnsField.filter(item => item.key === field);
      tempColumnsFilter.push({...oneColumn[0]});
    }
    let totalXOfFilter = 0;
    tempColumnsFilter.forEach(item => { 
      totalXOfFilter += item.width;
      delete item.render;
    });
    this.setState({
      dataFields,
      totalXOfFilter,
      columnsFilter: tempColumnsFilter,
    });
  };

  handleExport = async () => {
    const { paramSearch } = this.state;
    await exportStudentWithFields(paramSearch);
  };

  render() {
    const { 
      columnsField, 
      columnsFilter,
      dataFields, 
      loadingFields, 
      loadingStudents,
      totalXOfFields, 
      totalXOfFilter, 
      paramSearch, 
      rawDataFields,
      dataStudent,
    } = this.state;
    console.log('paramSearch',paramSearch);
    console.log('rawDataFields',rawDataFields);
    return (
      <Fragment>
        <Card title="Chn i tng chnh sch">
          <CartHeadAction>
            <BtnExport
              title="Xut Excel"
              onClick={this.handleExport}
            />
          </CartHeadAction>
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                bordered
                loading={loadingFields}
                dataSource={dataFields}
                columns={columnsField}
                scroll={{ x: totalXOfFields, y: 300 }}
                rowKey="customIdForThis"
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                bordered
                loading={loadingStudents}
                dataSource={dataStudent}
                columns={columnsFilter}
                scroll={{ x: totalXOfFilter, y: 300 }}
                rowKey="customIdForThis"
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSubsidyObject />
  </Page>
);



